.navbar .correct {
    -webkit-animation: .4s correct;
            animation: .4s correct;
  }
  
  .navbar .incorrect {
    -webkit-animation: .4s incorrect;
            animation: .4s incorrect;
  }
  
  @-webkit-keyframes correct {
    0% {
      color: white;
    }
  
    50% {
      color: #2e7d32;
    }
  
    100% {
      color: white;
    }
  }
  
  @keyframes correct {
    0% {
      color: white;
    }
  
    50% {
      color: #2e7d32;
    }
  
    100% {
      color: white;
    }
  }
  
  @-webkit-keyframes incorrect {
    0% {
      color: white;
    }
  
    50% {
      color: #c62828;
    }
  
    100% {
      color: white;
    }
  }
  
  @keyframes incorrect {
    0% {
      color: white;
    }
  
    50% {
      color: #c62828;
    }
  
    100% {
      color: white;
    }
  }
  
.navbar {
  background: #0f01ac;
  min-height: 70px;
  position: fixed;
  width: 100%;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
  color: white;
  font-size: calc(10px + (24 - 10) * (100vw - 320px) / (1200 - 400));
  z-index: 99;
  white-space: nowrap;
}

.navbar ul {
  margin: 0;
  padding: 0;
}

.navbar li {
  display: inline-block;
  padding: 26px 0px;
  width: 33.3%;
  text-align: center;
}

.navbar a {
  text-decoration: none;
  color: white;
  font-weight: 900;
}

@media (max-width: 420px) {
  .navbar li:first-child {
    width: 25%;
  }

  .navbar li:nth-child(2) {
    width: 35%;
  }

  .navbar li:last-child {
    width: 40%;
  }
}

.header {
  background-color: #2C323C;
  background-image: url("/assets/images/geometry.svg");
  background-repeat: repeat;
  color: white;
  min-height: 400px;
  padding: 180px 20px; 
  padding-bottom: 140px;
  text-align: center;
}

.header h1 {
  margin: 0;
  margin-bottom: 30px;
  font-size: 4rem;
}

.container {
    display: flex;
    min-height: 80%;
    flex-wrap: wrap;
    justify-content: center;
    padding-top: 50px;
    padding-bottom: 50px;
    width: 90%;
    margin: 0 auto;
  }
  
  @media (min-width: 1100px) {
    .container {
      padding-left: 100px;
      padding-right: 100px;
      width: 1100px;
    }
  }
  
.card {
    background: #fff;
    border-radius: 2px;
    height: 325px;
    margin: 1rem;
    position: relative;
    width: 325px;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    text-align: left;
  }
  
  .card > .img-container {
    height: 60%;
    overflow: hidden;
    text-align: center;
    background:rgb(0, 0, 0);
  }
  .card > .img-container > img {
    width: 60%
  }
  
  .card > .content {
    padding-left: 1rem;
    padding-right: 1rem;
    font-size: 15px;
  }
  
  .card > .content > ul {
    list-style-type: none;
  }
  
  .card > .content > ul > li {
    margin-top: 18px;
  }
  
  .card > .remove {
    color: #ff1744;
    font-size: 48px;
    position: absolute;
    top: -20px;
    right: 5px;
    cursor: pointer;
  }
  
  .card > .remove:hover {
    color: #d50000;
  }
  
.footer {
  background: 
  #6956AF;
  height: 100px;
  position: relative;
  color: white;
  overflow: auto;
